import '../styles/UserHeader.css'
import { ApiUserName } from '../api/ApiCall'
import { useParams } from 'react-router-dom'
import { useState , useEffect } from 'react'

function UserHeader() {
    // State de dpart : userName = null
    const [ userName , setUserName ] = useState(null)
    // Rcupration de l'id du user
    const userId = useParams().id

    useEffect(() => {
        const fetchUserName = async () => {
            const currentUserName = await ApiUserName(userId)
            setUserName(currentUserName)
            // UserName rcupr grce  l'appel fetch
        }

        fetchUserName()
    }, [userId])

    // Intgration du username rcupr
    return (
        <div className="user-header">
            <h1>Bonjour <span>{userName}</span></h1>
            <h3>Flicitations ! Vous avez explos vos objectifs hier </h3>
        </div>
    )
}

export default UserHeader